import React, { useEffect, useState, useMemo } from 'react';
import { useLocation } from '@reach/router';
import { navigate } from 'gatsby';
import {
  editSupplyListItems,
  fetchSupplyListDetails,
  fetchSupplyListMeta,
  saveSupplyListMeta,
} from '../tables/tables';

export default function ListDetail() {
  const location = useLocation();
  const pathParts = location.pathname.split('/').filter(Boolean);
  const rawId = pathParts[1] || '';
  const id = decodeURIComponent(rawId);

  const [items, setItems] = useState<any[]>([]);
  const [targetMargin, setTargetMargin] = useState<number | null>(null);
  const [loading, setLoading] = useState(true);
  const [saving, setSaving] = useState(false);
  const [marginDialogOpen, setMarginDialogOpen] = useState(false);
  const [marginInput, setMarginInput] = useState(30);
  const [success, setSuccess] = useState('');
  const [error, setError] = useState('');

  useEffect(() => {
    if (!id) return;

    const loadData = async () => {
      const [details, meta] = await Promise.all([
        fetchSupplyListDetails(id),
        fetchSupplyListMeta(id),
      ]);

      const enriched = details.items.map((row: any) => {
        const costEa = row.bulk / row.carton;
        const costTotal = costEa * row.qty;
        const totalRetail = row.sell * row.qty;
        const profit = (row.sell - costEa) * row.qty;
        return {
          ...row,
          costEa,
          costTotal,
          totalRetail,
          profit,
        };
      });

      setItems(enriched);
      setTargetMargin(meta?.desired_margin ?? null);
      setLoading(false);
    };

    loadData();
  }, [id]);

  const updateItem = (index: number, field: string, value: string | number) => {
    const updated = [...items];
    const parsedValue = parseFloat(value as string) || 0;
    updated[index][field] = parsedValue;

    const { bulk, carton, qty, sell } = updated[index];
    const costEa = bulk / carton;
    const costTotal = costEa * qty;
    const totalRetail = sell * qty;
    const profit = (sell - costEa) * qty;

    updated[index].costEa = costEa;
    updated[index].costTotal = costTotal;
    updated[index].totalRetail = totalRetail;
    updated[index].profit = profit;

    setItems(updated);
  };

  const applyMarginToAll = () => {
    const updated = items.map((item) => {
      const costEa = item.bulk / item.carton;
      const newSell = costEa / (1 - marginInput / 100);
      const profit = (newSell - costEa) * item.qty;
      return {
        ...item,
        sell: parseFloat(newSell.toFixed(2)),
        costEa,
        costTotal: costEa * item.qty,
        totalRetail: newSell * item.qty,
        profit,
      };
    });
    setItems(updated);
    setMarginDialogOpen(false);
  };

  const handleSave = async () => {
    setSaving(true);
    setError('');
    setSuccess('');

    const mapped = items
      .filter((i) => i.uid && typeof i.qty === 'number' && typeof i.sell === 'number')
      .map((i) => ({
        item_uid: i.uid,
        list_name: id,
        quantity: i.qty > 0 ? parseInt(i.qty, 10) : 1,
        sell: isNaN(i.sell) ? 0 : parseFloat(i.sell),
      }));

    if (mapped.length === 0) {
      setError('‚ùå No valid items to save.');
      setSaving(false);
      return;
    }

    const metaSaved = await saveSupplyListMeta(id, targetMargin ?? 0, 0);
    const ok = metaSaved && (await editSupplyListItems(id, mapped, true));

    if (ok) {
      setSuccess('‚úÖ Saved successfully!');
    } else {
      setError('‚ùå Failed to save.');
    }

    setSaving(false);
  };

  const totals = useMemo(() => {
    return items.reduce(
      (acc, row) => {
        acc.bulk += row.bulk;
        acc.cost += row.costEa * row.qty;
        acc.retail += row.sell * row.qty;
        acc.profit += row.profit;
        return acc;
      },
      { bulk: 0, cost: 0, retail: 0, profit: 0 }
    );
  }, [items]);

  const currentMargin = totals.retail > 0 ? (totals.profit / totals.retail) * 100 : 0;

  if (loading) return <div style={{ padding: 20 }}>Loading...</div>;

  return (
    <div style={{ padding: 20, fontFamily: 'Arial' }}>
      <h2>Supply List: {id}</h2>

      {/* <button onClick={() => setMarginDialogOpen(true)} style={{ marginBottom: 10 }}>
        üìà Set All to X% Profit Margin
      </button> */}

      {marginDialogOpen && (
        <div
          style={{
            padding: 10,
            background: '#eee',
            marginBottom: 20,
            border: '1px solid #aaa',
          }}
        >
          <label>
            <strong>Profit Margin (%): </strong>
          </label>
          <input
            type="number"
            value={marginInput}
            onChange={(e) => setMarginInput(parseFloat(e.target.value) || 0)}
            style={{ width: 60, marginLeft: 10 }}
          />
          <button onClick={applyMarginToAll} style={{ marginLeft: 10, padding: '4px 12px' }}>
            Apply
          </button>
          <button onClick={() => setMarginDialogOpen(false)} style={{ marginLeft: 10 }}>
            Cancel
          </button>
        </div>
      )}

      <table border={1} cellPadding={6} cellSpacing={0}>
        <thead>
          <tr>
            <th>ITEM</th>
            <th>BULK</th>
            <th>CARTON</th>
            <th>COST EA.</th>
            <th>COST TL.</th>
            <th>QTY</th>
            <th>SELL</th>
            <th>TOTAL</th>
            <th>PROFIT</th>
          </tr>
        </thead>
        <tbody>
          {items.map((row, idx) => (
            <tr key={row.sku}>
              <td
                onClick={() => navigate(`/items/${row.uid}`)}
                style={{ cursor: 'pointer', textDecoration: 'underline', color: '#007acc' }}
              >
                {row.item}
              </td>
              <td>{row.bulk}</td>
              <td>{row.carton}</td>
              <td>${row.costEa.toFixed(2)}</td>
              <td>${row.costTotal.toFixed(2)}</td>
              <td>
                <input
                  type="number"
                  value={row.qty}
                  onChange={(e) => updateItem(idx, 'qty', e.target.value)}
                  style={{ width: 60 }}
                />
              </td>
              <td>${row.sell.toFixed(2)}</td>
              <td>${row.totalRetail.toFixed(2)}</td>
              <td>${row.profit.toFixed(2)}</td>
            </tr>
          ))}

          <tr>
            <td colSpan={5}>
              <strong>Bulk Total</strong>
            </td>
            <td colSpan={4}>${totals.bulk.toFixed(2)}</td>
          </tr>
          <tr>
            <td colSpan={5}>
              <strong>Expenses</strong>
            </td>
            <td colSpan={4}>${totals.cost.toFixed(2)}</td>
          </tr>
          <tr>
            <td colSpan={5}>
              <strong>Revenue</strong>
            </td>
            <td colSpan={4}>${totals.retail.toFixed(2)}</td>
          </tr>
          <tr>
            <td colSpan={5}>
              <strong>Profit</strong>
            </td>
            <td colSpan={4}>${totals.profit.toFixed(2)}</td>
          </tr>
          <tr>
            <td colSpan={5}>
              <strong>Current Margin</strong>
            </td>
            <td colSpan={4}>{currentMargin.toFixed(2)}%</td>
          </tr>
        </tbody>
      </table>

      <button onClick={handleSave} disabled={saving} style={{ marginTop: 20, padding: '8px 16px' }}>
        {saving ? 'Saving...' : 'üíæ Save Changes'}
      </button>
      {error && <p style={{ color: 'red' }}>{error}</p>}
      {success && <p style={{ color: 'green' }}>{success}</p>}
    </div>
  );
}
